<template>
    <div style="margin-bottom: 20px;">
        <div class="info-block">
            <el-row :gutter="20">
                <el-col :span="8" class="info-col">
                    <img class="info-img" src="@/assets/user/version.png" />
                    <div class="info-subitem">
                        <div class="info-subitem-title">{{$t('VERSION')}}</div>
                        <span class="info-subitem-text">{{ version }}</span>
                    </div>
                </el-col>
                <el-col :span="8" class="info-col">
                    <img class="info-img" src="@/assets/user/healthStatus.png" />
                    <div class="info-subitem">
                        <div class="info-subitem-title">{{$t('HEALTH_STATUS')}}</div>
                        <span class="info-subitem-text">{{ healthStatus }}</span>
                    </div>
                </el-col>
                <el-col :span="8" class="info-col">
                    <el-button 
                        type="primary" 
                        plain 
                        style="margin-top: 10px;"
                        @click="handleConfirmAddUser"
                    >
                        {{ $t('BUTTON_ADD_USER') }}
                    </el-button>
                </el-col>
            </el-row>
        </div>

        <el-table
            :data="tableData"
            :span-method="objectSpanMethod"
            style="width: 100%; margin-top: 20px"
            :cell-style="{ textAlign: 'center', border: '0.5px solid rgb(123, 143, 175, 0.5)', padding: '10px 0', }"
            :header-cell-style="{ textAlign: 'center', padding: '10px 0', }"
        >
            <el-table-column prop="CertType" :label="$t('COLUMN_CERT_TYPE')"></el-table-column>
            <el-table-column prop="CertValidDate" :label="$t('COLUMN_CERT_VALID_DATE')"></el-table-column>
            <el-table-column prop="CertAlarm" :label="$t('COLUMN_CERT_ALARM')"></el-table-column>
            <el-table-column prop="CrlStatus" :label="$t('COLUMN_CRL_STATUS')"></el-table-column>
        </el-table>

        <el-table
            :data="userData"
            style="width: 100%; margin-top: 20px; margin-bottom: 20px;"
            :cell-style="{ textAlign: 'center', border: '0.5px solid rgb(123, 143, 175, 0.5)', padding: '10px 0', }"
            :header-cell-style="{ textAlign: 'center', padding: '10px 0', }"
        >
            <el-table-column prop="UserID" :label="$t('COLUMN_USER_ID')"></el-table-column>
            <el-table-column prop="UserName" :label="$t('COLUMN_USER_NAME')"></el-table-column>
            <el-table-column prop="Role" :label="$t('COLUMN_USER_TYPE')"></el-table-column>
            <el-table-column prop="CreateTime" :label="$t('COLUMN_CREATE_TIME')"></el-table-column>
            <el-table-column prop="operation" :label="$t('COLUMN_OPERATION')" width="120">
                <template slot-scope="scope">
                    <el-button @click="handleConfirmDelete(scope.row)" type="danger" :disabled="scope.row.RoleID === 1" plain size="small">
                        {{ $t('OPERATION_DELETE') }}
                    </el-button>
                </template>
            </el-table-column>
        </el-table>

        <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :total="userPagination.total"
            :page-size="userParams.PageSize"
            layout="prev, pager, next"
            style="padding-bottom: 30px;"
        >
        </el-pagination>

        <el-dialog
            :title="$t('CONFIRM_DELETE')"
            :visible.sync="isDelete"
            width="28%"
            :close-on-click-modal="false"
            :modal="false"
        >
            {{$t('CONFIRM_DELETE_TIP')}} {{ selectedRow.UserName }}?
            <span slot="footer" class="dialog-footer">
                <el-button @click="isDelete = false">{{$t('BTN_CANCEL')}}</el-button>
                <el-button type="primary" @click="handleDelete">{{$t('BTN_OK')}}</el-button>
            </span>
        </el-dialog>

        <user-manage :isShow="isAddUser" @handleCancel="handleCancel" :currOperation="'addUser'" />
    </div>
</template>

<script>
import { fetchVersion, fetchHealthStatus, fetchCertStatus, fetchUser, deleteUser } from '@/service/user.js'
import UserManage from '@/components/UserManage.vue';

export default {
    components: {
        UserManage,
    },
    name: 'user',
    data() {
        return {
            version: '',
            healthStatus: '',
            tableData: [],
            spanArr: [],
            position: 0,
            userData: [],
            userPagination: {
                total: 0,
            },
            userParams: {
                CurrentPage: 1,
                PageSize: 10,
                SortKey: '',
                SortType: ''
            },
            isDelete: false,
            selectedRow: {},
            isAddUser: false,
        };
    },
    mounted() {
        this.fetchData()
    },
    methods: {
        fetchData() {
            fetchVersion()
                .then(res => {
                    this.version = res.data.data.version.split('_')[0]
                })
            fetchHealthStatus()
                .then(res => {
                    this.healthStatus = res.data.msg === 'ok' ? '健康' : '不健康'
                })
            fetchCertStatus()
                .then(res => {
                    this.tableData = res.data.data
                    this.handleSpan()
                })
            this.fetchUserList()

        },
        fetchUserList() {
            fetchUser(this.userParams)
                .then(res => {
                    res.data.data.users.forEach(item => {
                        // RoleID = 1:管理员，=4 普通用户
                        item.Role = {1: '管理员', 4: '普通用户'}[item.RoleID]
                    })
                    this.userData = res.data.data.users
                    this.userPagination.total = res.data.data.total
                })
        },
        handleSpan() {
            let mgmtArr = this.tableData.filter(item => item.CertType === 'MGMT')
            let svcArr = this.tableData.filter(item => item.CertType !== 'MGMT')
            this.tableData = mgmtArr.concat(svcArr)

            this.tableData.forEach((item, index) => {
                item.CertType = {'MGMT': '管理面' , 'SVC': '服务面'}[item.CertType]
                item.CertAlarm = item.CertAlarm === '' ? '正常' : '不正常'
                item.CrlStatus = item.CrlStatus === 'No CRL certificate has been imported.' ? '未导入' : '已导入'
                if(index === 0) {
                    this.spanArr.push(1)
                    this.position = 0
                } else {
                    if(this.tableData[index].CertType === this.tableData[index - 1].CertType) {
                        this.spanArr[this.position] += 1;
                        this.spanArr.push(0)
                    } else {
                        this.spanArr.push(1)
                        this.position = index
                    }
                }
            })
        },
        objectSpanMethod({ row, column, rowIndex, columnIndex }) {
            if (columnIndex === 0) {
                const _row = this.spanArr[rowIndex]
                const _col = _row > 0 ? 1 : 0;
                return {
                    rowspan: _row,
                    colspan: _col
                }
            }
        },
        handleConfirmDelete(row) {
            this.selectedRow = row
            this.isDelete = true
        },
        handleDelete(){
            deleteUser(this.selectedRow.UserName)
                .then(res => {
                    if(res.data.status === '00000000') {
                        this.$message({
                            message: this.$t('SUCCESS_DELETE'),
                        })
                        this.isDelete = false
                        this.fetchData()
                    } else {
                        this.$message({
                            message: this.$t('ERR_DELETE') + '。' + this.$t('ERR_DELETE_USER'),
                        })
                        this.isDelete = false
                    }
                })
                .catch(err => {
                    this.isDelete = false
                })
        },
        handleConfirmAddUser() {
            this.isAddUser = true
        },
        handleCancel() {
            this.isAddUser = false
            this.fetchData()
        },
        handleSizeChange(val) {
            this.userParams.PageSize = val
            this.fetchUserList()
        },
        handleCurrentChange(val) {
            this.userParams.CurrentPage = val
            this.fetchUserList()
        }
    }
}
</script>
<style scoped>
.info-block {
    background-color: #1f2329;
    height: 64px;
    border-radius: 4px;
    padding: 16px 24px;
}

.info-col {
    display: flex;
    vertical-align: center;
}
.info-subitem {
    display: inline-block;
}

.info-subitem-title {
    margin-left: 10px;
    font-size: 14px;
    color: #6A6A6A;
}

.info-subitem-text {
    margin-left: 10px;
    font-size: 32px;
    color: #6A6A6A;
}
</style>


